body {
    display: flex;
    margin-top: 50px;
    justify-content: center;
    background-color: #FFFCFF;
}

button {
    margin-right: 2.5px;
    margin-left: 2.5px;
    width: 75px;
    cursor: pointer;
    box-shadow: 0px 0px 2px gray;
    border: none;
    outline: none;
    padding: 5px;
    border-radius: 5px;
    color: white;
}

#header {
    padding: 10px;
    color: #FFC333;
    font-size: 26px;
    font-family: sans-serif;
    display: flex;
    justify-content: space-between;
}

    #header button {
        background-color: #FFC333;
    }

#container {
    width: 770px;
}


#sunday {
    padding-left: 20px;
}

#monday {
    padding-left: 25px;
}

#tuesday {
    padding-left: 15px;
}

#wednesday {
    padding-left: 7px
}

#thursday {
    padding-left: 27px;
}

#friday {
    padding-left: 30px;
}

#saturday {
    padding-left: 13px;
}

#weekdays {
    width: 100%;
    display: flex;
    color: black;
}

    #weekdays div {
        width: 100px;
        padding-bottom: 10px;
        padding-top: 10px;
    }

#calendar {
    width: 100%;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
}

.day {
    width: 100px;
    padding: 10px;
    height: 100px;
    cursor: pointer;
    box-sizing: border-box;
    background-color: white;
    margin: 5px;
    box-shadow: 0px 0px 3px #CBD4C2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

    .day:hover {
        background-color: #c2adf5;
    }

    .day + .currentDay {
        background-color: #660099;
        color: white;
    }

.event {
    font-size: 15px;
    padding: 3px;
    background-color: #6bb2ff;
    color: white;
    border-radius: 5px;
    max-height: 40px;
    overflow: hidden;
}

    .event.completed {
        background-color: forestgreen;
    }

    .event.late {
        background-color: orangered;
    }

    .event.overdue {
        background-color: gold;
    }

.padding {
    cursor: default !important;
    background-color: #FFFCFF !important;
    box-shadow: none !important;
}

#newEventModal, #deleteEventModal {
    z-index: 20;
    padding: 25px;
    background-color: #c2adf5;
    box-shadow: 0px 0px 3px black;
    border-radius: 5px;
    width: 350px;
    top: 100px;
    left: calc(50% - 175px);
    position: absolute;
    font-family: sans-serif;

}

#eventTitleInput {
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 25px;
    border-radius: 3px;
    outline: none;
    border: none;
    box-shadow: 0px 0px 3px grey;
}

    #eventTitleInput.error {
        border: 2px solid red;
    }

#cancelButton, #deleteButton, #detailsButton {
    background-color: #660099;
}

#saveButton, #closeButton {
    background-color: #FFC333;
}

#eventText {
    font-size: 14px;
}

#modalBackDrop {
    top: 0px;
    left: 0px;
    z-index: 10;
    width: 100vw;
    height: 100vh;
    position: absolute;
    background-color: rgba(0,0,0,0.8);

}

.loader {
    margin: 100px auto;
    font-size: 25px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    text-indent: -9999em;
    -webkit-animation: load5 1.1s infinite ease;
    animation: load5 1.1s infinite ease;
